.content p {
	padding-bottom: 1.75rem;
	text-align: justify;
	hyphens: auto;
	
	&:last-of-type {
		padding-bottom: 0;
	}
	
	&.lead {
		@media (--mobile), (max-height: 500px) {
			padding-top: 1.2rem;	
		}

		&:first-letter {
			font-weight: 700;
			font-size: 3.8rem;
			line-height: 0;
		}
	}
}
