/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

html,
body {
	height: 100%;
}

html {
	overflow: hidden;
	font-size: 100%;
	box-sizing: border-box;
}

*,
*:before,
*:after {
	box-sizing: inherit;
}

body {
	overflow: scroll;
	-webkit-overflow-scrolling: touch;
	line-height: 1.4;
	font-family: 'PT Serif', Georgia, serif;
	background: #fff;
}

h1,
h2,
h3,
h4,
h5,
h6,
b,
strong {
	font-weight: 700;
}

img {
	display: block;
	max-width: 100%;
	width: 100%;
}

.article {
}

.article__main {
	background: #fff;
}

.aside-image-signature {
	font-size: 12.8px;
	font-size: 0.8rem
}

@media screen and (max-width: 79.9375rem) {

	.aside-image-signature {
		max-width: 20.125rem;
		margin: auto;
	}
	}

.aside-image {
}

@media screen and (max-width: 79.9375rem) {

	.aside-image {
		max-width: 20.125rem;
		margin-left: auto;
		margin-right: auto;
	}
	}

@media (max-width: 25rem) {

	.aside-image {
		position: relative;
		left: calc(-50vw + 50%);
		max-width: none;
		width: 100vw;
	}
	}

.aside-image:not(:last-child) {
	margin-bottom: 20px;
	margin-bottom: 1.25rem;
}

.aside-image:last-child {
	margin-bottom: 5vh;
}

.aside-image__signature {
	margin: 20px auto 0;
	margin: 1.25rem auto 0;
	font-size: 12.8px;
	font-size: 0.8rem;
}

@media (max-width: 25rem) {

	.aside-image__signature {
		padding: 0 10%;
	}
}

p + .content__aside > .aside-image {
}

@media screen and (max-width: 47.9375rem) {

	p + .content__aside > .aside-image {
		margin-top: calc(5vh - 1.75rem);
	}
	}

p:last-of-type + .content__aside > .aside-image {
}

@media screen and (max-width: 47.9375rem) {

	p:last-of-type + .content__aside > .aside-image {
		margin-top: 5vh;
		margin-bottom: 0;
	}
	}

.content {
	max-width: 1000px;
	max-width: 62.5rem;
	margin: auto;
	padding: 8vh 40px 10vh;
	padding: 8vh 2.5rem 10vh;
	font-size: 19.2px;
	font-size: 1.2rem
}

@media screen and (max-width: 47.9375rem) {

	.content {
		max-width: 34.375rem;
		padding-left: 5%;
		padding-right: 5%;
	}
	}

.content__inner {
	position: relative;
	padding-right: 45%;
}

@media screen and (max-width: 47.9375rem) {

	.content__inner {
		padding-right: 0;
	}
}

.content__aside {
	position: absolute;
	right: 0;
	width: 35%;
}

@media screen and (max-width: 47.9375rem) {

	.content__aside {
		position: static;
		width: 100%;
	}
}

.content__aside--top {
	top: 0;
}

.content__aside--bottom {
	bottom: 0;
}

.fullsize-image {
	height: 100vh;
	-o-object-fit: cover;
	   font-family: "object-fit:cover";
	   object-fit: cover;
}

.heading-2 {
	font-size: 32px;
	font-size: 2rem;
	line-height: 1.25;
	padding-bottom: 18px;
	padding-bottom: 1.125rem
}

@media screen and (max-width: 79.9375rem) {

	.heading-2 {
		padding-bottom: 1.375rem;
	}
	}

@media screen and (max-width: 47.9375rem) {

	.heading-2 {
		padding-bottom: 1.8125rem;
	}
	}

.hero-section {
	position: relative;
	overflow: hidden;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	width: 100%;
	text-align: center;
	color: #fff;
	background-size: cover;
	background-position: center
}

.hero-section::before {
	content: '';
	position: absolute;
	z-index: 1;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: rgba(0, 0, 0, .2);
}

.hero-section__bg {
	position: fixed;
	z-index: -1;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-size: cover;
	background-position: center;
	will-change: transform;
}

.hero-section__inner {
	position: relative;
	z-index: 2;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	width: 100%;
	min-height: 80vh;
	padding: 0 16px;
	padding: 0 1rem;
}

.hero-section__main,
	.hero-section__caption {
	width: 100%;
}

.hero-section__main {
	-webkit-box-flex: 1;
	    -ms-flex-positive: 1;
	        flex-grow: 1;
	-ms-flex-negative: 1;
	    flex-shrink: 1;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	    -ms-flex-direction: column;
	        flex-direction: column;
	-webkit-box-align: center;
	    -ms-flex-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
}

.hero-section__heading,
	.hero-section__description {
	width: 100%;
	padding: 12.5px 0;
	padding: .78125rem 0;
}

@media screen and (max-width: 47.9375rem) {

	.hero-section__heading,
	.hero-section__description {
		padding: .28125rem 0;
	}
}

.hero-section__heading {
	font-size: 49.6px;
	font-size: 3.1rem;
	line-height: 1.6;
	letter-spacing: .32em;
}

@media screen and (max-width: 47.9375rem) {

	.hero-section__heading {
		font-size: 1.2rem;
		line-height: 2.1;
	}
}

.hero-section__heading--lead {
	font-size: 72px;
	font-size: 4.5rem;
	letter-spacing: .48em;
}

@media screen and (max-width: 47.9375rem) {

	.hero-section__heading--lead {
		font-size: 2.5rem;
		letter-spacing: .4em;
	}
}

.hero-section__description {
	font-weight: 700;
	letter-spacing: .7em;
}

@media screen and (max-width: 47.9375rem) {

	.hero-section__description {
		font-size: 0.8rem;
		letter-spacing: .5em;
	}
}

.hero-section__caption {
	padding: 16px 0 30px;
	padding: 1rem 0 1.875rem;
	font-size: 12.8px;
	font-size: .8rem;
	line-height: 1.6;
	white-space: normal;
}

@media screen and (max-width: 79.9375rem) {

	.hero-section__caption {
		padding: 1.25rem 0 2.5rem;
	}
}

@media screen and (max-width: 47.9375rem) {

	.hero-section__caption {
		padding: 1.1rem 0 2.2rem;
	}
}

.hero-section a {
	color: inherit;
	text-decoration: none;
}

.hero-section a:hover {
	text-decoration: underline;
}

.content p {
	padding-bottom: 28px;
	padding-bottom: 1.75rem;
	text-align: justify;
	-webkit-hyphens: auto;
	    -ms-hyphens: auto;
	        hyphens: auto
}

.content p:last-of-type {
	padding-bottom: 0;
}

.content p.lead {
}

@media screen and (max-width: 47.9375rem), (max-height: 500px) {

	.content p.lead {
		padding-top: 1.2rem;
	}
}

.content p.lead:first-letter {
	font-weight: 700;
	font-size: 60.8px;
	font-size: 3.8rem;
	line-height: 0;
}

.blockquote {
	font-size: 12.8px;
	font-size: .8rem
}

.blockquote--big {
	font-size: 16px;
	font-size: 1rem;
	font-weight: 700;
}

@media screen and (max-width: 47.9375rem) {

	.blockquote {
		max-width: 350px;
		margin: auto;
		padding-left: 5%;
		padding-right: 5%;
	}
	}

p + .content__aside > .blockquote {
	padding-top: calc(5vh - 1.75rem);
}

p:last-of-type + .content__aside > .blockquote {
	padding-top: 5vh;
}

.content__aside:not(:last-child) .blockquote {
	padding-bottom: 5vh;
}

.blockquote__inner {
	position: relative
}

.blockquote__inner::before {
	content: '';
	position: absolute;
	top: 0;
	bottom: 0;
	left: -20px;
	left: -1.25rem;
	width: 4px;
	background: #f0f0e8;
}
